"use client"
import { useState, useEffect } from "react"
import { Settings2 } from "lucide-react"
import Dropdown from "@/components/Dropdown"

export default function SettingsMenu() {
  const [isOpen, setIsOpen] = useState(false)
  const [temperature, setTemperature] = useState(0.5)
  const [model, setModel] = useState("mistralai/Mistral-7B-Instruct-v0.1")
  const [llmRerank, setLlmRerank] = useState(true)
  const [tone, setTone] = useState("neutral")
  const [autoScroll, setAutoScroll] = useState(true)

const temperatureStops = [
  { value: 0.1129, label: "Precise" },
  { value: 0.29032, label: "Focused" },
  { value: 0.46774, label: "Informative" },
  { value: 0.64516, label: "Conversational" },
  { value: 0.82258, label: "Exploratory" },
  { value: 1.0, label: "Creative" }
]
function getTemperatureLabel(value: number): string {
  const match = temperatureStops.find((stop) => stop.value === value)
  return match ? match.label : "Unknown"
}

  useEffect(() => {
    const config = { temperature, model, llmRerank, tone, autoScroll }
    fetch("http://localhost:8000/set-config", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(config),
    })
    }, [temperature, model, llmRerank, tone, autoScroll])

  return (
    <div className="absolute top-6 left-6">
      <button
        className="p-2 rounded-full hover:bg-neutral-200 dark:hover:bg-neutral-700"
        onClick={() => setIsOpen(!isOpen)}
      >
        <Settings2 size={20} />
      </button>

      {isOpen && (
        <div className="mt-2 p-5 bg-white dark:bg-neutral-950 border border-neutral-100 dark:border-neutral-900 rounded-3xl shadow-lg w-72">
        {/* Tone Selector */}
          <div className="mb-7">
            <label className="block mb-2 text-sm font-medium text-neutral-700 dark:text-neutral-300">Tone</label>
            <div className="relative">
                <select
              value={tone}
              onChange={(e) => setTone(e.target.value)}
              className="
                    w-full 
                    appearance-none 
                    p-3 
                    shadow-inner
                    rounded-full 
                    text-sm 
                    text-neutral-700 
                    dark:text-neutral-300
                    font-medium 
                    focus:outline-none 
                    bg-neutral-100
                    dark:bg-neutral-800 
                    hover:bg-neutral-300 
                    dark:hover:bg-neutral-700"
            >
              <option value="formal">Formal</option>
              <option value="neutral">Neutral</option>
              <option value="casual">Casual</option>
            </select>
            <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center px-3 text-neutral-500">
                <svg className="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
                </svg>
            </div>
            </div>
          </div>

            {/* Model Selector */}
            {/* <div className="mb-7">
                <label className="block mb-2 text-sm font-medium text-neutral-700 dark:text-neutral-300">Model</label>
                <div className="relative">
                    <select
                    value={model}
                    onChange={(e) => setModel(e.target.value)}
                    className="
                    w-full 
                    appearance-none 
                    p-3 
                    shadow-inner
                    rounded-full 
                    text-sm 
                    text-neutral-700 
                    dark:text-neutral-300
                    font-medium 
                    focus:outline-none 
                    bg-neutral-100
                    dark:bg-neutral-800 
                    hover:bg-neutral-300 
                    dark:hover:bg-neutral-700"
                    >
                    <option value="mistralai/Mistral-7B-Instruct-v0.1">Mistral</option>
                    </select>
                    <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center px-3 text-neutral-500">
                        <svg className="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
                        </svg>
                    </div>
                </div>
            </div> */}

          {/* Temperature Slider */}
          <div className="mb-7">
            <div className="flex justify-between">
                <label className="block mb-2 text-sm font-medium text-neutral-800 dark:text-neutral-200">
                    Response Style
                </label>
                <p className="block mb-2 text-sm font-medium text-neutral-500 dark:text-neutral-500">{getTemperatureLabel(temperature)}</p>
            </div>
            {/* Background track */}
            <div className="relative w-full h-7 rounded-full bg-neutral-100 dark:bg-neutral-800 shadow-inner">
                <div
                className="absolute h-7 rounded-full bg-green-500 shadow-md"
                style={{ width: `${temperature * 100}%` }}
                />

                {/* Transparent slider overlaid */}
                <input
                type="range"
                min="0.1129"
                max="1"
                step="0.17742"
                value={temperature}
                onChange={(e) => setTemperature(parseFloat(e.target.value))}
                className="absolute top-0 left-0 w-full h-3 opacity-0 cursor-pointer"
                />
            </div>
            <style jsx>{`
                input[type='range']::-webkit-slider-thumb {
                -webkit-appearance: none;
                appearance: none;
                width: 0;
                height: 0;
                }
                input[type='range']::-moz-range-thumb {
                width: 0;
                height: 0;
                background: transparent;
                border: none;
                }
            `}</style>
          </div>

          {/* LLM Rerank Toggle (Modern Switch) */}
            <div className="flex justify-between items-center">
            <label className="text-sm font-medium text-neutral-800 dark:text-neutral-200">
                LLM Reranking (Slower)
            </label>
            <button
                onClick={() => setLlmRerank(!llmRerank)}
                className={`w-11 h-7 flex items-center shadow-inner rounded-full p-1 transition-colors duration-300
                ${llmRerank ? "bg-green-500" : "bg-neutral-100 dark:bg-neutral-800"}`}
            >
                <div
                className={`w-6 h-6 bg-white rounded-full shadow-md transform transition-transform duration-300
                    ${llmRerank ? "translate-x-3.5" : "translate-x-[-1.5px]"}`}
                />
            </button>
            </div>
        </div>
      )}
    </div>
  )
}
